  display: none; }

#sun {
  -webkit-animation: sunrise 10s;
          animation: sunrise 10s; }

@-webkit-keyframes sunrise {
  from {
    -webkit-transform: translateY(20%);
            transform: translateY(20%); }
  to {
    -webkit-transform: translateY(0);
            transform: translateY(0); } }

@keyframes sunrise {
  from {
    -webkit-transform: translateY(20%);
            transform: translateY(20%); }
  to {
    -webkit-transform: translateY(0);
            transform: translateY(0); } }

g {
  -webkit-transition: -webkit-transform 0.5s ease-out;
  transition: -webkit-transform 0.5s ease-out;
  transition: transform 0.5s ease-out;
  transition: transform 0.5s ease-out, -webkit-transform 0.5s ease-out; }

#ground4 {
  -webkit-transform: translateX(calc(var(--mouse-x) * 0.002px));
          transform: translateX(calc(var(--mouse-x) * 0.002px)); }

#ground3 {
  -webkit-transform: translateX(calc(var(--mouse-x) * 0.005px));
          transform: translateX(calc(var(--mouse-x) * 0.005px)); }

#ground2 {
  -webkit-transform: translateX(calc(var(--mouse-x) * 0.008px));
          transform: translateX(calc(var(--mouse-x) * 0.008px)); }

#ground1, #trees, .home__aboutteam__svg > #webengers {
  -webkit-transform: translateX(calc(var(--mouse-x) * 0.01px));
          transform: translateX(calc(var(--mouse-x) * 0.01px)); }

#cloud1 {
  -webkit-animation: cloudmove1 300s linear infinite;
          animation: cloudmove1 300s linear infinite; }

#cloud2 {
  -webkit-animation: cloudmove2 200s linear infinite;
          animation: cloudmove2 200s linear infinite; }

@-webkit-keyframes cloudmove1 {
  from {
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%); }
  to {
    -webkit-transform: translateX(120%);
            transform: translateX(120%); } }

@keyframes cloudmove1 {
  from {
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%); }
  to {
    -webkit-transform: translateX(120%);
            transform: translateX(120%); } }

@-webkit-keyframes cloudmove2 {
  from {
    -webkit-transform: translateX(-80%);
            transform: translateX(-80%); }
  to {
    -webkit-transform: translateX(70%);
            transform: translateX(70%); } }

@keyframes cloudmove2 {
  from {
    -webkit-transform: translateX(-80%);
            transform: translateX(-80%); }
  to {
    -webkit-transform: translateX(70%);
            transform: translateX(70%); } }

