#tak {
    display: none;
}
#sun {
    animation: sunrise 10s;
}
@keyframes sunrise {
    from {
        transform: translateY(20%);
    }
    to {
        transform: translateY(0);
    }
}
@mixin mtmove($mod) {
    transform: translateX(calc(var(--mouse-x) * #{$mod}px));
}
g {
    transition: transform 0.5s ease-out;
}
#ground4 {
    @include mtmove(0.002);
}
#ground3 {
    @include mtmove(0.005);
}
#ground2 {
    @include mtmove(0.008);
}
#ground1, #trees, .home__aboutteam__svg > #webengers {
    @include mtmove(0.01);
}
#cloud1 {
    animation: cloudmove1 300s linear infinite;    
}
#cloud2 {
    animation: cloudmove2 200s linear infinite;
}
@keyframes cloudmove1 {
    from {
        transform: translateX(-50%);
    }
    to {
        transform: translateX(120%);
    }
}
@keyframes cloudmove2 {
    from {
        transform: translateX(-80%);
    }
    to {
        transform: translateX(70%);
    }
}